    commentary:
      "।।14.2।। व्याख्या, इदं ज्ञानमुपाश्रित्य, पूर्वश्लोक में भगवान ने उत्तम और पर, इन दो विशेषणोंसे जिस ज्ञानकी महिमा कही थी? उस ज्ञानका अनुभव करना ही उसका आश्रय लेना है। उस ज्ञानका अनुभव होने से मनुष्य के सम्पूर्ण संशय मिट जाते हैं और वह ज्ञानस्वरूप हो जाता है।मम साधर्म्यमागताः, उस ज्ञानका आश्रय लेकर मनुष्य मेरी सधर्मताको प्राप्त हो जाते हैं अर्थात् जैसे मेरेमें कर्तृत्वभोक्तृत्व नहीं है? ऐसे ही उनमें भी कर्तृत्वभोक्तृत्व नहीं रहता। जैसे मैं सदा ही निर्लिप्तनिर्विकार रहता हूँ? ऐसे ही उनको भी अपनी निर्लिप्ततानिर्विकारताका अनुभव हो जाता है।ज्ञानी महापुरुष भगवान के समान निर्लिप्तनिर्विकार तो हो जाते हैं? पर वे भगवान के समान संसारकी उत्पत्ति? पालन और संहारका कार्य नहीं कर सकते। हाँ? योगाभ्यासके बलसे किसी योगीमें कुछ सामर्थ्य आ जाती है? पर वह सामर्थ्य भी भगवान की सामर्थ्यके समान नहीं होती। कारण कि वह युञ्जान योगी है अर्थात् उसने अभ्यास करके कुछ सामर्थ्य प्राप्त की है। परन्तु भगवान् युक्त योगी हैं अर्थात् भगवान्में सामर्थ्य सदा से स्वतःसिद्ध है। भगवान् सब कुछ करने में समर्थ हैं, कर्तुमकर्तुमन्यथा कर्तुं समर्थः। योगीकी सामर्थ्य तो, सीमित होती है? पर भगवान की सामर्थ्य असीम होती है।सर्गेऽपि नोपजायन्ते, यहाँ अपिपदसे यह मालूम होता है कि वे ज्ञानी महापुरुष महासर्गके आरम्भ में भी उत्पन्न नहीं होते। महासर्गके आदि में चौदह लोकोंकी तथा उन लोकोंके अधिकारियोंकी उत्पत्ति होती है? पर वे महापुरुष उत्पन्न नहीं होते अर्थात् उनको फिर कर्मपरवश होकर शरीर धारण नहीं करना पड़ता।प्रलये न व्यथन्ति च, महाप्रलयमें संवर्तक अग्निसे चरअचर सभी प्राणी भस्म हो जाते हैं। समुद्रके बढ़ जानेसे पृथ्वी डूब जाती है। चौदह लोकोंमें हलचल? हाहाकार मच जाता है। सभी प्राणी दुःखी होते हैं? नष्ट होते हैं। परन्तु महाप्रलयमें उन ज्ञानी महापुरुषोंको कोई दुःख नहीं होता? उनमें कोई हलचल नहीं होती? विकार नहीं होता। वे महापुरुष जिस तत्त्वको प्राप्त हो गये हैं? उस तत्त्वमें हलचल? विकार है ही नहीं? तो फिर वे महापुरुष व्यथित कैसे हो सकते हैं नहीं हो सकते।महासर्गमें भी उत्पन्न न होने और महाप्रलयमें भी व्यथित न होने का तात्पर्य यह है कि ज्ञानी महापुरुषका प्रकृति और प्रकृतिजन्य गुणों से सर्वथा सम्बन्धविच्छेद हो जाता है। इसलिये प्रकृतिका सम्बन्ध रहनेसे जो जन्ममरण होता है? दुःख होता है? हलचल होती है? प्रकृतिके सम्बन्धसे रहित महापुरुषमें वह जन्ममरण? दुःख आदि नहीं होते। सम्बन्ध, जो भगवान की सधर्मताको प्राप्त हो जाते हैं? वे तो महासर्गमें भी पैदा नहीं होते परन्तु जो प्राणी महासर्गमें पैदा होते हैं? उनके उत्पन्न होने की क्या प्रक्रिया है, इसको आगे के श्लोक में बताते हैं।",
