    commentary:
      "।।14.27।। व्याख्या, ब्रह्मणो हि प्रतिष्ठाहम्, मैं ब्रह्मकी प्रतिष्ठा? आश्रय हूँ, ऐसा कहने का तात्पर्य ब्रह्मसे अपनी अभिन्नता बतानेमें है। जैसे जलती हुई अग्नि साकार है और काष्ठ आदि में रहनेवाली अग्नि निराकार है, ये अग्निके दो रूप हैं? पर तत्त्वतः अग्नि एक ही है। ऐसे ही भगवान् साकाररूप से हैं और ब्रह्म निराकररूप से है, ये दो रूप साधकोंकी उपासनाकी दृष्टि से हैं? पर तत्त्वतः भगवान् और ब्रह्म एक ही हैं? दो नहीं। जैसे भोजनमें एक सुगन्ध होती है और एक स्वाद होता है नासिकाकी दृष्टि से सुगन्ध होती है और रसनाकी दृष्टि से स्वाद होता है? पर भोजन तो एक ही है। ऐसे ही ज्ञानकी दृष्टि से ब्रह्म है और भक्तिकी दृष्टि से भगवान् हैं? पर तत्त्वतः भगवान् और ब्रह्म एक ही हैं।भगवान् कृष्ण अलग हैं और ब्रह्म अलग है, यह भेद नहीं है किन्तु भगवान् कृष्ण ही ब्रह्म हैं और ब्रह्म ही भगवान् कृष्ण है। गीता में भगवान ने अपने लिये ब्रह्म शब्द का भी प्रयोग किया है, ब्रह्मण्याधाय कर्माणि (5। 10) और अपने को अव्यक्तमूर्ति भी कहा है, मया ततमिदं सर्वं जगदव्यक्तमूर्तिना (9। 4)। तात्पर्य है कि साकार और निराकार एक ही हैं? दो नहीं।अमृतस्याव्ययस्य च, अविनाशी अमृतका अधिष्ठान मैं ही हूँ और मेरा ही अधिष्ठान अविनाशी अमृत है। तात्पर्य है कि अविनाशी अमृत और मैं, ये दो तत्त्व नहीं हैं? प्रत्युत एक ही हैं। इसी अविनाशी अमृतकी प्राप्तिको भगवान ने अमृतमश्नुते (13। 12 14। 20) पदसे कहा है।शाश्वतस्य च धर्मस्य, सनातन धर्म का आधार मैं हूँ और मेरा आधार सनातन धर्म है। तात्पर्य है कि सनातन धर्म और मैं, ये दो नहीं हैं? प्रत्युत एक ही हैं। सनातन धर्म मेरा ही स्वरूप है। गीता में अर्जुन ने भगवान को शाश्वतधर्म का गोप्ता (रक्षक) बताया है (11। 18)। भगवान् भी अवतार लेकर सनातन धर्म की रक्षा किया करते हैं (4। 8)।सुखस्यैकान्तिकस्य च, ऐकान्तिक सुख का आधार मैं हूँ और मेरा आधार ऐकान्तिक सुख है अर्थात् मेरा ही स्वरूप ऐकान्तिक सुख है। भगवान ने इसी ऐकान्तिक सुखको अक्षय सुख (5। 21)? आत्यन्तिक सुख (6। 21) और अत्यन्त सुख (6। 28) नाम से कहा है।इस श्लोक में ब्रह्मणः? अमृतस्य आदि पदोंमें राहोः शिरः की तरह अभिन्नतामें षष्ठी विभक्तिका प्रयोग किया गया है। तात्पर्य है कि राहुका सिर, ऐसा जो प्रयोग होता है? उसमें राहु अलग है और सिर अलग है, ऐसी बात नहीं है? प्रत्युत राहुका नाम ही सिर है और सिरका नाम ही राहु है। ऐसे ही यहाँ ब्रह्म? अविनाशी अमृत आदि ही भगवान् कृष्ण हैं और भगवान् कृष्ण ही ब्रह्म? अविनाशी अमृत आदि हैं।ब्रह्म कहो? चाहे कृष्ण कहो? और कृष्ण कहो? चाहे ब्रह्म कहो अविनाशी अमृत कहो? चाहे कृष्ण कहो? और कृष्ण कहो चाहे अविनाशी अमृत कहो शाश्वत धर्म कहो? चाहे कृष्ण कहो और कृष्ण कहो चाहे शाश्वत धर्म कहो ऐकान्तिक सुख कहो चाहे कृष्ण कहो और कृष्ण कहो चाहे ऐकान्तिक सुख कहो एक ही बात है। इसमें कोई आधारआधेय भाव नहीं है? एक ही तत्त्व है। इसलिये भगवान की उपासना करने से ब्रह्मकी प्राप्ति होती है, यह बात ठीक ही है।इस प्रकार ? तत्? सत्, इन भगवन्नामोंके उच्चारणपूर्वक ब्रह्मविद्या और योगशास्त्रमय श्रीमद्भगवद्गीतोपनिषद्रूप श्रीकृष्णार्जुनसंवादमें गुणत्रयविभागयोग नामक चौदहवाँ अध्याय पूर्ण हुआ।।14।।, ",
